@import url('https://fonts.googleapis.com/css2?family=Poppins&display=swap');

* {
  font-family: 'Poppins', sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background:linear-gradient(165deg, #099ec7, #0b0c0e);
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
}

.App {
  text-align: center;
}

h1 {
  color: #099ec7;
  margin-bottom: 0.5rem;
  font-size: 1.75rem;
}

.TodoWrapper {
  background: #0b0c0e;
  padding: 2rem;
  border-radius: 15px;
}

.TodoForm {
  width: 100%;
  margin-bottom: 1rem;
}

.todo-input {
  height: 3rem;
  outline: none;
  background: none;
  border: 3px  solid #099ec7;
  padding: 0.5rem 1rem;
  margin-top: 1rem;
  margin-bottom: 2rem;
  width: 300px;
  color: #fff;
  border-radius: 10px 30px 10px 30px;
}

.todo-input::placeholder {
  color: #ffffff4d;
}

.todo-btn {
  background: #099ec7;
  color: #fff;
  border-radius: 10px 20px 10px 20px;
  padding: 0.55rem;
  cursor: pointer;
  margin-left: 15px;
  height: 3rem;
  transition: transform 0.5s ease, background-color 0.5s ease;
}

.todo-btn:hover {
  transform: scale(1.05);
  background-color: #fff;
  color: #099ec7;
}

.todo-btn:active {
  transform: scale(0.95); 
  background-color: #007a9a; 
  color: #fff; 
}


.Todo {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: #007a9a;
  padding: 0.75rem 1rem;
  border-radius: 10px 25px 10px 25px;
  margin-bottom: 1rem;
  transition: transform 0.5s ease, background-color 0.5s ease;
}

.Todo:hover{
  transform: scale(1.05);
  background-color: #122142b0;
  color: #099ec7;
}

.fa-trash {
  margin-left: 0.75rem;
}

.incompleted {
  color: #fff;
  cursor: pointer;
}

.completed {
  color: #099ec7;
  text-decoration: line-through;
  cursor: pointer;
}

.edit-icon,
.delete-icon {
  cursor: pointer;
  transition: color 0.3s ease; 
}

.edit-icon:hover,
.delete-icon:hover {
  color: #fff; 
}


.edit-icon:active,
.delete-icon:active {
  transform: scale(0.9); 
  color: #007a9a; 
}
